import { Button } from '@material-ui/core';
import React, { useRef, useState} from 'react';
import { useTheme } from "@material-ui/styles";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import $ from 'jquery';
import TextField from '@material-ui/core/TextField';
import { Typography } from '@material-ui/core';
import Checkbox from '@material-ui/core/Checkbox';
import CircularProgress from '@material-ui/core/CircularProgress';
import { FormControlLabel } from '@material-ui/core';
import { Switch,Radio,RadioGroup } from '@material-ui/core';
export default function JsonExporter() {

  const json = useRef()
  const wynik = useRef()
  var text;
    function Action()
    {
      var jsonstrict = json.current.value;
      var json2 = JSON.parse(jsonstrict)
    text = objectsToCSV(json2.sellers);
    //wynik.current.innerText = text;
     download_txt();
    }
    function objectsToCSV(arr) {
      const array = [Object.keys(arr[0])].concat(arr)
      return array.map(row => {
          return Object.values(row).map(value => {
              return typeof value === 'string' ? JSON.stringify(value) : value
          }).toString()
      }).join('\n')
  }
  function download_txt() {
    var textToSave = text;
  var hiddenElement = document.createElement('a');

  hiddenElement.href = 'data:attachment/text,' + encodeURI(textToSave);
  hiddenElement.target = '_blank';
  hiddenElement.download = 'Json.csv';
  hiddenElement.click();
}
  
return (
    <div >
      <br></br>
       <TextField variant="outlined" inputRef={json} rows={10} fullwidth multiline placeholder="Insert valid SELLERS.JSON file here" id="tocheck" style={{borderRadius:"10px",textAlign:"center",width:"100%",height:"200px",resize:"vertical"}} className="inline-txtarea"></TextField>
    <p> </p> 
    <p> </p>
    <br></br>
    <Button variant="contained" color="primary" onClick={Action}>Export to CSV</Button>
    <p> </p>
    <div ref={wynik}>

    </div>
    </div>
);
}